@-webkit-keyframes hike {
0% {
    -webkit-transform: translate(0, 0) scale(1);
            transform: translate(0, 0) scale(1);
}
33% {
    -webkit-transform: translate(20%, -30%) scale(0.8);
            transform: translate(20%, -30%) scale(0.8);
}
66% {
    -webkit-transform: translate(20%, -40%) scale(0.6);
            transform: translate(20%, -40%) scale(0.6);
}
100% {
    -webkit-transform: translate(5%, -50%) scale(0.4);
            transform: translate(5%, -50%) scale(0.4);
}
}
@keyframes hike {
0% {
    -webkit-transform: translate(0, 0) scale(1);
